export default {
  name: 'detailedWrapper',
  data (){
    return {
      list: [],
      sum: '',//总积分
    }
  },
  methods: {
    //获取数据
    getIntegralData(){
      this.http.get(this.ports.integral.loglist, res =>{
        this.$store.commit('changeLoading',false)
        console.log(res)
        if(res.success){
          let data = res.data
          this.list = data.res
          this.sum = data.sum
        }else{
          this.showToastTxtOnly(res.msg)
        }
      })
    },
  },
  mounted (){
    this.getIntegralData()
  }
}



